@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;200;300;400&display=swap);
html {
    --color0: #FFD951;
    --color1: #32DAFF;
    --color2: #3BF384;
    --color3: #F19FFF;
    --color4: #AE92FF;
    --color5: #FFFFFF;
    --color6: #9C9C9C;
    --color7: #FF9900;
    --color8: #5e6eff;
    --color9: #D46A83;
}

.color0 {
    background: #FFD951;
    background: var(--color0);
}

.color1 {
    background: #32DAFF;
    background: var(--color1);
}

.color2 {
    background: #3BF384;
    background: var(--color2);
}

.color3 {
    background: #F19FFF;
    background: var(--color3);
}

.color4 {
    background: #AE92FF;
    background: var(--color4);
}

.color5 {
    background: #FFFFFF;
    background: var(--color5);
}

.color6 {
    background: #9C9C9C;
    background: var(--color6);
}

.color7 {
    background: #FF9900;
    background: var(--color7);
}

.color8 {
    background: #5e6eff;
    background: var(--color8);
}

.color9 {
    background: #D46A83;
    background: var(--color9);
}
.items-list {
    overflow-y: scroll;
    width: 100%;
    max-height: 25vh;
    font-size: 2rem;
    border: var(--base-border);
    background: var(--high-color);
    box-shadow: var(--main-shadow);
}

.items-list-item {
    width: 100%;
    height: 3rem;
    padding: 1vh 0 2.5vh 0;
    font-size: 2rem;
    border: 0;
    border-bottom: var(--base-border);
    border-radius: 0;
}

.items-list-item:last-child {
    border-bottom: 0;
}

table {
    width: 100%;
    margin: 0 auto 2rem auto;
    border-collapse: collapse;
}

tr {
    height: 5rem;
}

td {
    font-size: 3rem;
    border: var(--base-border);
    box-shadow: var(--main-shadow);
}

.main-cell {
    width: 5rem;
}

.type-cell {
    font-size: 2rem;
}

.type-cell.grey {
    color: #797979;
}

.type-cell.full-grey {
    color: #b8b8b8;
    background: #b8b8b8;
}

.week {
    margin: 0 auto 5vh auto;
}

.week-date {
    border: none;
}

.week-date div {
    height: 4rem;
    font-family: var(--main-font);
    font-weight: 400;
    font-size: 2.5rem;
    line-height: 4rem;
    padding: 0;
    background: var(--base-color);
    border: none;
}

.day {
    width: 5rem;
    background: var(--high-color);
}

.left-column {
    text-align: left;
    padding: 0 0 0 10px;
}

.week-item-delete {
    float: left;
    width: 3rem;
    height: 3rem;
    margin: auto 5px auto auto;
    font-size: 2rem;
    line-height: 2rem;
}

.week-number-arrow-up {
    background: rgba(255, 0, 0, 0.4) url(/turtledot/static/media/arrowUp.db38afd6.svg) center/cover;
}

.week-number-arrow-down {
    background: rgba(255, 0, 0, 0.4) url(/turtledot/static/media/arrowDown.2182d902.svg) center/cover;
}

.week-spots {
    background: rgba(255,255,255,0.5);
}

.allDone, .allDone .week-spots {
    color: var(--high-color);
    background: var(--line-color);
}

.spot {
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 100%;
}

.spot.grey {
    border: 2px solid #797979;
    background: #c1c1c1;
}

.spot.open {
    border: var(--main-border);
    background: var(--high-color);
}

.spot.closed {
    border: var(--main-border);
    background: var(--line-color);
}

.allDone .spot.closed {
    border: #1dff75;
    background: #1dff75;
}

.scooch {
    box-shadow: -2px -2px 2px 0 var(--line-color);
    transform: translate(2px, 2px);
    transition: all ease-in-out;
}

.item {
    display: flex;
    justify-content: space-between;
    margin: 0 0 2rem 0;
}

.item input {
    height: 5rem;
    font-family: var(--main-font);
    font-weight: 400;
    font-size: 3rem;
    line-height: 5rem;
    padding: 0;
    background: var(--high-color);
    border: var(--base-border);
}

.item .text {
    width: calc(33rem - 35px);
    height: 5rem;
    font-size: 3rem;
    line-height: 5rem;
    text-align: left;
    padding: 0 0 0 5px;
    background: var(--high-color);
    border: var(--base-border);
    box-shadow: var(--main-shadow);
}

.item .number {
    width: 8rem;
    border-radius: 5rem;
    -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

button.delete {
    transform: rotate(-45deg);
}

.palette {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    background: var(--high-color);
    padding: 0.5rem;
}

.palette-square {
    width: 10.5rem;
    height: 10.5rem;
    margin: 0.5rem auto;
    border-radius: 0;
}

.addAllItems {
    width: 100%;
    color: var(--base-color);
    background: var(--line-color);
    border-radius: 0;
}

.addAllItems.copy {
    background: #240050;
}
nav {
    display: flex;
    justify-content: space-evenly;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    min-height: 50px;
    max-height: 200px;
    background: var(--high-color);
    box-shadow: 0 -5px 10px 0 rgba(0, 104, 14, 0.2);
}

a {
    color: var(--line-color);
    text-decoration: none;
}

.menu-item {
    width: 20vw;
    height: auto;
    border: none;
}

.menu-icon {
    font-size: 5rem;
    font-weight: 300;
    border-radius: 100%;
}

.menu-icon span {
    display: inline-block;
    width: 2rem;
    height: 2rem;
    background: #c1c1c1;
    border-radius: 100%;
}

.menu-icon span.menu-icon-doit {
    background: var(--line-color);
}

.menu-text {
    font-size: 1.5rem;
    line-height: 5rem;
    text-transform: uppercase;
}


.here {
    color: #1dff75;
}

.menu-item.here .menu-icon span {
    background: #1dff75;
    border-color: #1dff75;
}

header {
    text-align: center;
}

.logo {
    margin: 1rem;
    width: 12rem;
}

.clear {
    display: flex;
    flex-direction: column;
    margin: 10vh auto;
}

button.clear-button {
    width: 150px;
    height: 40px;
    margin: 20px auto;
    border-radius: 20px;
}

@media screen and (min-width: 800px) {

    button.clear-button {
        width: 300px;
    }

}

.bgPanel {
  z-index: -1;
  position: fixed;
  width: 80vw;
  height: 100vh;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #fff;
  opacity: 0.3;
  box-shadow: var(--main-shadow);
}

h1 {
  margin: 2rem;
  font-size: 4rem;
  font-weight: 400;
  text-transform: uppercase;
}

h2 {
  margin: 3rem;
  font-size: 3rem;
  font-weight: 300;
  text-transform: uppercase;
}

main {
  margin: 0 auto 12vh auto;
}

button {
  cursor: pointer;
  width: 5rem;
  height: 5rem;
  font-family: var(--main-font);
  font-weight: 400;
  font-size: 3rem;
  padding: 0;
  background: var(--high-color);
  border: var(--base-border);
  border-radius: 100%;
  box-shadow: var(--main-shadow);
}

button.plus-sign {
  font-size: 5rem;
  font-weight: 300;
  line-height: 0;
}

button.add {
  margin: 2vh auto;
}

button.delete, .delete {
  transform: rotate(-45deg);
}

.done {
  background: var(--line-color);
}

.edit-box {
  width: 100%;
  margin: 0 auto 2rem auto;
}

.edit-box-buttons {
  height: 7rem;
}

button.edit-complete {
  float: right;
  margin: 1rem;
}

@media screen and (min-height: 800px) {

  main {
    max-width: 800px;
  }

}
* {
  --base-color: #efefef;
  --high-color: #ffffff;
  --line-color: #000000;
  --main-font: 'Josefin Sans', sans-serif;
  --main-border: 2px solid #000000;
  --base-border: 1px solid #efefef;
  --main-shadow: 0 20px 20px 0 rgba(0, 104, 14, 0.2);
  font-size: 1.6vw;
  font-family: var(--main-font);
  text-align: center;
  box-sizing: border-box;
  margin: auto;
}

body {
  min-height: 100vh;
  background: linear-gradient(to bottom right, #bae1ff, #daffe9);
}

@media screen and (min-height: 800px) {
  * {
    font-size: 10px;
  }
}
