.clear {
    display: flex;
    flex-direction: column;
    margin: 10vh auto;
}

button.clear-button {
    width: 150px;
    height: 40px;
    margin: 20px auto;
    border-radius: 20px;
}

@media screen and (min-width: 800px) {

    button.clear-button {
        width: 300px;
    }

}
